.home {
  &__title {
    position: fixed;
    top: 15%;
    left: 50%;
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 12px;
    transform: translateX(-50%);
  }
  &__subtitle {
    position: fixed;
    top: 25%;
    left: 50%;
    font-size: 32px;
    font-weight: lighter;
    letter-spacing: 12px;
    transform: translateX(-50%);
  }
  &__logo {
    position: fixed;
    top: 50px;
    right: 30px;
    width: 60px;
    height: 60px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__downtown {
    position: fixed;
    bottom: 0;
    width: 100%;
    img {
      width: 100%;
    }
  }
  &__house {
    position: fixed;
    bottom: 80px;
    left: 200px;
    width: 281px;
    height: 186px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__workers {
    position: fixed;
    bottom: 20px;
    left: 500px;
    width: 137px;
    height: 180px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__bike {
    position: fixed;
    bottom: 100px;
    right: 300px;
    width: 154px;
    height: 95px;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
